import { useEffect } from "react";
import { Helmet } from "react-helmet";
import { GetUri } from "../../Constants"
import LoadableImage from '../UI/LodableImage';
import { PageViews } from "@piwikpro/react-piwik-pro";

export default function MorningAfterRain(){

    var sunrise1 = GetUri("18now6ndpwntzzo/32.webp");
    var sunrise2 = GetUri("t4n147pqis1x6fw/29.webp");
    var deers = GetUri("0xc2wxsohkp8kxc/14.webp");
    var deer= GetUri("9wfh8f32eipl7t4/13.webp");

    useEffect(() => {
        PageViews.trackPageView('Ráno po daždi');
    }, [])
    
    return <>
        <Helmet>
            <title>Ráno po daždi</title>
            <meta
                name="description"
                content="Fotenie jeleňov po ráno po daždi"
            />
            <meta name="author" content="Dalibor Kundrat" />
            <meta
                name="keywords"
                content="rano jelene, rano po daždi, vysoká zver, jelene, jelen posed"
            />
        </Helmet>
        <div className="space-y-5 text-base lg:text-lg">

            <LoadableImage className="rounded-sm shadow-sm" src={GetUri("bee7c7priesi909/31.webp")} alt="Nocna burka"/>

            <h1 className="text-xl font-semibold">Ráno po daždi</h1>

            <p>Vstávam ráno pred štvrtou a vravím si: <i className="italic font-semibold">"Nechce sa my"</i>. Trochu sa premáham, predsa som sa až tak dobre nevyspal. O polnoci som ešte počúval prichádzajúce búrku a hrmenie s ňou sprevádzané. Práve kôly nočnej búrke, ktorá priniesla vodu po dlhotrvajúcom suchu, môže byt ranná scenéria sprevádzanou menšou hmlou a oparom, ktorý je skvelý ako jemne pozadie s peknou vlhkou dynamikou.</p>
            <p>Nechystám sa ďaleko, skôr na istotu, že nejakú vysokú uvidím.</p>
            <p>Vyrážam na miesto, kde už týždeň po večeroch monitorujem jeleniu zver a jej správanie v období sucha za vysokých denných teplôt.</p>
            <p>Len včera večer sa mi podarilo nafotiť pekne zábery priamo v kukuričnom poli, neďaleko miesta kam práve kráčam.</p>
            <p>Je šero, svitať začína už o štvrtej, je obdobie najdlhších dni.</p>
            <p>Fotiť je možné cca. Od 4:30, čaká ma ešte kus cesty. Nezobral som statív, v podstate nič len foťák a dlhé sklo (70-200). Kontrolujem časy a spolieham sa, že to udržím. Odhadujem, že po výstupe kopca to bude 1/80 až 1/100 pri ISO600 no s pribúdajúcim časom sa to zrýchli.</p>
            <p>Hneď po výšľape kopca spomalím a spozorniem. Počujem aj vlastný buchot srdca. <i className="font-semibold">"Pomaly ďalej zájdeš"</i> a <i className="font-semibold">"Trpezlivosť ruže prináša"</i> je to, čo teraz platí. Nesmiem sa chovať akoby som tu bol len kôly fotke. Ta možno bude odmenou, zato, ako sa v lese budem chovať.</p>
            <p>Za prvým medzi-horizontom predomnou ma byť posed. Nedávno som tam zazrel dvoch šestnastorákov päsť sa v repkovom poli... Hneď za posedom ústí na lúku zvierací chodník a už v minuloti som tam pozoroval zver prechadzajucu z jedného lesa do druhého v skorých ranných hodinách.</p>
            <div className="w-full justify-center content-center">
                <iframe
                    className="w-full h-80 md:h-96"
                    src="https://www.youtube.com/embed/vxlxgts3mZA" 
                    title="Starsie jelene poranu" 
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen />    
            </div>
            <p>V okolí nič nepočuť len štebotanie ranných vtákov, zvuk lesa a otravne ovady, ktoré ma práve začali neskutočne otravovať, sakra, kde sa tu tak skoro vzali?</p>
            <p>Vidím ich! Práve vyšli 3 kusy z lesa. Sú, ale ešte ďaleko cca 200m predomnou. Rozhodujem sa priblížiť, čo to pôjde.</p>
            <p>Jelene majú poránu jedinú starosť, poriadne sa nažrať, a tak predpokladam, že si ma nevšimnú.</p>
            <p>Ráno sú naozaj menej obozretne, nebývajú v tento čas bežné rušene, ako tomu býva podvečer, kedy sledujú všetko okolo a vyhodnocujú každý zvuk a pohyb. Už nejakú hodinku pobehujú a mali tak dostatok času adaptovať sa na zvuky prostredia.</p>
            <p>Pokrčený sa pomaly približujem z ľavá ma kryje repka z práva les. Kráčam bez obáv, nakoľko pršalo, pod nohami nič nešuští a nepraská. Navyše v lese ešte počuť dopadať kvapky vody, čo zakrýva drobný šum.</p>
            <p>Zrazu pri zemi, asi 5m predomnou vykukne z poľa zvieracia hlava. Vravím si, <i>"asi jazvec"</i>... <i>"no prd"</i>... Vybehne srnec a pozeráme na seba...</p>
            <p>Samozrejme tvárim sa ako strom (haha)... Nezožral to, aj keď mám maskovanie... Skočil do lesa a začala pobrechavat... Nepáčilo sa mu to.  Momentálne je ich poľovná sezóna a akoby sa po nich zľahla zem. Vraví sa, že to vedia a <i>"Ahaho"</i> musí sa objaviť práve teraz. Nuž priestor je to jeho a ja som tu ten neželaný hosť. Už nič nenarobím, nečakal som ho a evidentne ani on mňa.</p>
            <p>Zdvíham pomaly hlavu nad repku a snažím sa zistiť, čo nato jelene, pri ktorých som menej než 100m.</p>
            <p>Na brechot našťastie nezareagovali poobzerali sa a ďalej pokračovali v pastve.</p>
            <p>To, že nie vždy jelene reagujú na výstražné signály srnčej zvery (ako je brechot) už pozorujem opakovane a nijak konkrétne ma to neprekvapí. Opačne to, ale neplatí! Srnčia zver reaguje veľmi pozorne na jelenie výstražné signály. Práve, preto sa pri nich cíti bezpečne. Dalo sa, teda aj predpokladať, že nejaká ta srnčia v ich okolí bude.</p>
            <p>Podarilo sa mi priblížiť na cca. 50m. To je asi tentokrát maximum, pretože jeden z mladších jeleňov si ma už všimol no nevie ma identifikovať, preto zvedavo pózuje a ja zatiaľ fotím par pekných záberov s poľovníckym posedom v jeho pozadí.</p>
            
            <LoadableImage className="rounded-sm shadow-sm" src={deer} alt="Pred posedom"/>

            <p>Sledujem, čo bude ďalej. Priblížiť sa už nemôžem vyplašil by som ich, a preto čakám. Po krátkom čase sa vydávajú k druhému lesu.</p>
            <p>Keď nehrozí, že ich vyplaším, zdvihnem hlavu nad repku a cca. 50m odomna je ďalších sedem kusov, ktorým z repky, trčí len hlava s parožím.</p>
            
            <LoadableImage className="rounded-sm" src={deers} alt="Crieda jelenov"/>

            <p>Počas približovania som ich vôbec nevidel pravdepodobne polihavali a až brechot srnčej alebo pohyb starších ich mohol postaviť.  Nieje to prvýkrát, čo bola vysoká vo večšom počte na par metrov a vôbec ju nebolo počuť. Je to fascinujúce s akou tichosťou sa dokážu v takom poraste pohybovať. Repka je pomerne hustá a dáva im istú mieru bezpečia, vlk by sa tam hádam načisto zamotal a možno by do nej ani nevošiel. Sám som si to raz chcel cez podobne pole <i>"Skrátiť"</i> a par metrov ma stalo riadne množstvo energie.</p>
            <p>Po krátkom čase jelene pomaly zachádzajú do lesa a ja si vravím, že ešte zbehnem pozrieť kukuričné pole z predošlého večera.</p>
            <p>Bol to pokus... Nič konkrétne nevidím a po 10minútach sledovania zahajujem návrat.</p>
        
            <p>Je 5:40 a ja sa pomaly vraciam pri východe slnka s ďalším zvečneným zážitkom. Teším sa na teplú kavej, ktorú si hneď po príchode vychutnám.</p>
            
            <LoadableImage className="rounded-sm" src={sunrise1} alt="Vychod slnka"/>
            
            <LoadableImage className="rounded-sm" src={sunrise2} alt="Vychod slnka"/>
            
        </div>      
    </>
}